            description: '\uC9C0\uC9C0\uC790\uB4E4\uACFC\xA0\uC9C8\uC758\uC751\uB2F5\uD558\uB294\xA0"\uBC18\uAC12 \uB4F1\uB85D\uAE08,\xA0\uB300\uD559 \uC11C\uC5F4\uD654 \uD3D0\uC9C0 \uB4F1\uC744 \uAE30\uD544\uCF54 \uC774\uB904\uB0B4\uACA0\uB2E4"\uACE0 \uBC1D\uD614\uB2E4. 2012\uB144 18\uB300 \uB300\uC120 \uACF5\uC57D\uC73C\uB85C \uB0B4\uC138\uC6E0\uB358\xA0\u25B2\u2018\uB4F1\uB85D\uAE08 \uC0C1\uD55C\uC81C\u2019\xA0\uB3C4\uC785\uC73C\uB85C\xA0\uBC18\uAC12 \uB4F1\uB85D\uAE08\xA0\uC2E4\uD604,\xA0\u25B2\u2018\uACE0\uB4F1\uAD50\uC721\uC7AC\uC815 \uAD50\uBD80\uAE08 \uBC95\u2019\xA0\uC81C\uC815,\xA0\u25B2\u2018\uB4F1\uB85D\uAE08 \uC2EC\uC758\uC704\uC6D0\uD68C\u2019\xA0\uD559\uC0DD\xB7\uAD50\uC9C1\uC6D0\xB7\uC804\uBB38\uAC00 \uCC38\uC5EC \uD655\uB300\uC640 \uAE30\uB2A5 \uAC15\uD654, \u25B2\uC800\uC18C\uB4DD\uCE35 \uD559\uC0DD\uC5D0\uAC8C \uAD6D\uAC00\uC7A5\uD559\uAE08 \uC9C0\uC6D0 \uB4F1\uC758 \uC5F0\uC7A5 \uC120\uC0C1\uC73C\uB85C \uD574\uC11D\uB41C\uB2E4.\xA0',
