            description: '\uBD80\uB3D9\uC0B0 \uBCF4\uC720\uC138\uB780 \uC7AC\uC0B0\uC138(\uC9C0\uBC29\uC138)\uC640 \uC885\uD569\uBD80\uB3D9\uC0B0\uC138(\uAD6D\uC138)\uB85C \uC774\uB8E8\uC5B4\uC9C4 \uAC83\uC73C\uB85C, \uD604\uC7AC \uD55C\uAD6D\uC758 \uBD80\uB3D9\uC0B0 \uBCF4\uC720\uC138\uAC00 \uACBD\uC81C\uD611\uB825\uAC1C\uBC1C\uAE30\uAD6C(OECD) \uC18C\uC18D \uAD6D\uAC00\uB4E4\uBCF4\uB2E4 \uB0AE\uB2E4\uBA70 \uB192\uC5EC\uC57C \uD55C\uB2E4\uB294 \uC785\uC7A5\uC744 \uCDE8\uD588\uB2E4.',
