            description: '\uC815\uBD80\uC758 \uC8FC\uD0DD\uC815\uCC45\uACFC \uC5F0\uACC4\uD558\uC5EC,\xA0\uC11C\uBBFC \uC784\uB300\uC8FC\uD0DD\uC744\xA0\uC9C0\uC18D\uC801\uC73C\uB85C \uACF5\uAE09\uD558\uACE0, \uB300\uD559\uC0DD,\xA0\uC0AC\uD68C \uCD08\uB144\uC0DD,\xA0\uC2E0\uD63C\uBD80\uBD80 \uB4F1\uC744 \uC704\uD55C \uD589\uBCF5\uC8FC\uD0DD\uC744 \uAC74\uC124\uD588\uB2E4.',
