            summary: '\u201C\uC784\uB300\uC8FC\uD0DD \uC8FC\uBBFC\uB4E4\uB85C\uBD80\uD130 \uAC10\uC0AC\uD328\u201D',
            description: '2011\uB144 \uC8FC\uBBFC\uC5F0\uB300 \uB300\uD45C\uB4E4\uACFC\uC758 \uBA74\uB2F4 \uD6C4, \uCDA9\uCCAD\uB0A8\uB3C4\uB294\xA0\uACF5\uACF5 \uAC74\uC124\xA0\uC784\uB300\uC8FC\uD0DD \uAD00\uB828 3\uAC1C \uBC95 \uAC1C\uC815\uC5D0 \uC801\uADF9 \uC9C0\uC6D0\uD588\uB2E4. \uBC95 \uAC1C\uC815\uC73C\uB85C \uC784\uB300 \uBCF4\uC99D\uAE08\uACFC \uC8FC\uAC70\uAD8C\uC744 \uBCF4\uD638\uBC1B\uC744 \uC218 \uC788\uAC8C \uB418\uC790, \uC8FC\uBBFC\uC5F0\uB300\uAC00 \uC774\uC5D0 \uB300\uD55C \uAC10\uC0AC\uC758 \uB73B\uC744 \uC804\uD558\uAE30 \uC704\uD574 \uC548\uD76C\uC815 \uCDA9\uB0A8\uC9C0\uC0AC\uC5D0\uAC8C \uAC10\uC0AC\uD328\uB97C \uC218\uC5EC\uD55C \uBC14 \uC788\uB2E4',
            where: '\uC804\uAD6D \uBD80\uB3C4\xA0\uACF5\uACF5 \uAC74\uC124\xA0\uC784\uB300\uC8FC\uD0DD \uC8FC\uBBFC\uC5F0\uB300, \uAC10\uC0AC\uD328 \uC218\uC5EC (2013)',
