            description: '\uC784\uAE08\uC744 \uADFC\uC18D\uC5F0\uC218\uC640 \uC9C1\uAE09 \uAE30\uC900\uC774 \uC544\uB2CC 1\uB144 \uB3D9\uC548\uC758 \uAC1C\uC778\uBCC4 \uC131\uACFC\uC5D0 \uB530\uB77C \uCC28\uB4F1\uC744 \uB450\uB294 \uC81C\uB3C4\uC778 \uC131\uACFC\uC5F0\uBD09\uC81C\uB294 \uADFC\uB85C\uC790 \uC9C0\uC704\uC5D0 \uC911\uB300\uD55C \uC601\uD5A5\uC744 \uBBF8\uCE58\uB294 \uC0AC\uC548\uC774\uC9C0\uB9CC, \uB178\uC0AC \uD569\uC758 \uC5C6\uC774 \uC801\uC6A9\uB41C \uACF3\uC774 \uB9CE\uB2E4\uBA70, \uB300\uD1B5\uB839\uC5D0 \uB2F9\uC120\uB418\uBA74 \uC131\uACFC\uC5F0\uBD09\uC81C \uC790\uCCB4\uB97C \uD3D0\uC9C0\uD558\uACA0\uB2E4\uACE0 \uD655\uC5B8\uD588\uB2E4.',
