            description: '\uB2E4\uC74C\uACFC \uAC19\uC740 \uC774\uC720\uB85C \uB9E4\uB2C8\uD398\uC2A4\uD1A0\xA0\uC77C\uC790\uB9AC \uBD80\uBD84\xA0\uCD5C\uC6B0\uC218\uC0C1\uC744 \uC218\uC0C1\uD55C \uBC14 \uC788\uB2E4. \u25B2\uC2DC\uBBFC \uAE30\uC5C5 104\uAC1C \uC721\uC131 \u25B21,189\uBA85 \uC77C\uC790\uB9AC \uCC3D\uCD9C \u25B2\uACF5\uACF5\uBD80\uBB38 \uBE44\uC815\uADDC\uC9C1 607\uBA85 \uC815\uADDC\uC9C1 \uC804\uD658 \u25B2\uAE30\uC5C5 \uC720\uCE58\uC640 \uBCA4\uCC98\uAE30\uC5C5 1000\uAC1C \uB3CC\uD30C \u25B2\uAD00\uAE09\uACF5\uC0AC 14\uB9CC 9485\uBA85 \uC131\uB0A8 \uC2DC\uBBFC \uACE0\uC6A9 \u25B2\uBBFC\uAC04 \uAC74\uCD95\uACF5\uC0AC\uC7A5\uC758 27\uB9CC 2600\uBA85 \uC131\uB0A8 \uC2DC\uBBFC \uACE0\uC6A9 \u25B2\uAE30\uC5C5\uACFC\uC758 \uC735\uD569\uD589\uC815\uC744 \uD1B5\uD55C \uB2E4\uBB38\uD654\uAC00\uC815 \uC5EC\uC131\uC758 \uC77C\uC790\uB9AC \uCC3D\uCD9C \u25B2\uC77C\uC790\uB9AC \uACF5\uC2DC \uBAA9\uD45C\uB97C 118% \uCD08\uACFC \uB2EC\uC131',
            where: '\uB9E4\uB2C8\uD398\uC2A4\uD1A0\xA0\uC77C\uC790\uB9AC \uBD80\uBD84\xA0\u201C\uCD5C\uC6B0\uC218\uC0C1\u201D \uC218\uC0C1 (2012/2013)',
            type: '\uC131\uB0A8\uC2DC \uC815\uCC45',

