            description: '\uADFC\uB85C\uC790\uC5D0\uAC8C \uCDA9\uBD84\uD55C \uD734\uC2DD\uACFC \uC7AC\uCDA9\uC804\uC758 \uC2DC\uAC04\uC744 \uBCF4\uC7A5\uD558\uAE30 \uC704\uD574 \uC815\uC2DC \uD1F4\uADFC\uC744 \uBCF4\uC7A5\uD558\uACE0 \uC57C\uADFC\uC744 \uC81C\uD55C\uD558\uB294\n  \uB4F1\uC758 \uC8FC\uC694 \uC815\uCC45 \uBC29\uD5A5\uC744 \uB2F4\uC740 \uBC95\uC548\uC778 \uCE7C\uD1F4\uADFC\xA0\uBCF4\uC7A5 \uBC95\uC744\xA0\uC81C\uC548\uD588\uB2E4. \uBC95\uC548\uC758 \uC138\uBD80\uB0B4\uC6A9\uC73C\uB85C\uB294 \u25B2\uD1F4\uADFC \uD6C4 SNS \uB4F1\uC744 \uD1B5\uD574 \uC5C5\uBB34\uC9C0\uC2DC\uB97C \uD558\uB294\xA0\'\uB3CC\uBC1C \uB178\uB3D9\'\uC5D0\xA0\uB300\uD55C \uCD08\uACFC\uADFC\uB85C\uC2DC\uAC04 \uD3B8\uC785 \uBC0F \uC774\uC5D0 \uB530\uB978 \uD560\uC99D\uC784\uAE08 \uC9C0\uBD88 \u25B2 \uADFC\uB85C\uC77C\n  \uC0AC\uC774\xA0\'\uCD5C\uC18C \uD734\uC2DD\uC2DC\uAC04\'(\uD1F4\uADFC\xA0\uD6C4 \uCD5C\uC18C 11\uC2DC\uAC04) \uBCF4\uC7A5\uC81C \uB3C4\uC785 \u25B2 1\uB144 \uB2E8\uC704 \uCD5C\uB300 \uADFC\uB85C\uC2DC\uAC04 \uADDC\uC815 \uB3C4\uC785 \uB4F1\uC758 \uAD6C\uC0C1\uC774 \uD3EC\uD568\uB410\uB2E4.',
