            description: '\uB178\uB7C9\uC9C4\n  \uACE0\uC2DC\uD559\uC6D0\uACFC \uC2E0\uB9BC\uB3D9 \uACE0\uC2DC\uCD0C\uC744 \uCC3D\uC5C5\uC758 \uC694\uB78C\uC778 \uC2E4\uB9AC\uCF58\uBC38\uB9AC\uAC00 \uB418\uB294 \uC2DC\uB300\uB97C \uC5F4\uC5B4\uC57C \uD55C\uB2E4\uBA70, \uC0AC\uD68C\uC801 \uC548\uC804\uD310\uC73C\uB85C\uC11C\uC758 \'\uD601\uC2E0 \uC548\uC804\uB9DD\' \uAD6C\uCD95\uC774 \uBB34\uC5C7\uBCF4\uB2E4\uB3C4 \uC911\uC694\uD558\uB2E4\uACE0 \uB9D0\uD588\uB2E4. \uCC3D\uC5C5\uC790\uAC00 \uCC3D\uC5C5\uBE44\uC6A9\uC744 \uB9C8\uB828\uD558\uAE30 \uC704\uD574 \uC790\uC2E0\uACFC \uAC00\uC871\uB4E4\uC758 \uC790\uC0B0\uC744\n  \uB2F4\uBCF4\uB85C \uBE5A\uC744 \uB0B4\uB294 \'\uC735\uC790\' \uBC29\uC2DD\uC5D0\uC11C \uBC97\uC5B4\uB098 \uC804\uBB38\uC131 \uC788\uB294 \uD22C\uC790\uC790\uB4E4\uB85C\uBD80\uD130 \uC720\uD55C \uCC45\uC784\uD558\uC5D0 \uD22C\uC790\uB97C \uBC1B\uC744 \uC218 \uC788\uB3C4\uB85D \'\uD22C\uC790\' \uC911\uC2EC\uC758 \uD658\uACBD\uC744 \uB9CC\uB4E4\uACA0\uB2E4\uACE0 \uBC1D\uD614\uB2E4.',
