            description: '"\uACE0\uC6A9 \uBCF4\uD5D8\uC758\xA0\uC721\uC544\uD734\uC9C1 \uD61C\uD0DD\uC744 \uBABB \uBC1B\uB294 \uACF3\uC744 \uC704\uD574 \uBD80\uBAA8\uBCF4\uD5D8\uC744 \uB9CC\uB4E4\uC5B4 \uAD6D\uAC00\uAC00 \uC0C1\uB2F9 \uBD80\uBD84\uC744 \uC9C0\uC6D0, \uC721\uC544\uD734\uC9C1\uC218\uB2F9\uC744 \uC9C0\uAE09\uD558\uB294 \uC81C\uB3C4\uB97C \uC0C8\uB85C \uB3C4\uC785\uD558\uB294 \uAC8C\xA0\uB9DE\uB294\uB2E4\uACE0 \uBCF8\uB2E4.\xA0\uAE30\uC874 \uACE0\uC6A9\uBCF4\uD5D8\uC740\n  \uADF8\uB300\uB85C \uB450\uB418, \uACE0\uC6A9\uBCF4\uD5D8\uC774 \uAC00\uC785\uB418\uC9C0 \uC54A\uC740 \uC5F4\uC545\uD55C \uC0AC\uC5C5\uC7A5\uC744 \uC704\uD55C \uC81C\uB3C4\uB97C \uC815\uBD80 \uCC28\uC6D0\uC5D0\uC11C \uC9C0\uC6D0\uD558\uACA0\uB2E4\uB294 \uAC83\uC774 \uBD80\uBAA8 \uBCF4\uD5D8\uC758 \uCDE8\uC9C0\uC774\uB2E4.\u201D',
