            description: '\uBBFC\uAC04 \uAE30\uC5C5 \uADFC\uB85C\uC790\uB4E4\uC758 \uC721\uC544\uD734\uC9C1\uC744 \uACF5\uACF5\uBD80\uBB38 \uADFC\uB85C\uC790\uB4E4\uCC98\uB7FC \uCD5C\uC7A5 3\uB144 \uBCF4\uC7A5\uD558\uBA70, \uC0AC\uC6A9 \uD69F\uC218\uB3C4 \uCD5C\uB300 3\uD68C\uAE4C\uC9C0 \uB098\uB220 \uC790\uC720\uB86D\uAC8C \uC0AC\uC6A9\uD560 \uC218 \uC788\uB3C4\uB85D \uD558\uB294 \uB0B4\uC6A9\uC744 \uB2F4\uC558\uB2E4.',
