            description: '\uC721\uC544\uD734\uC9C1 \uAE09\uC5EC\uB97C \uD604\uC2E4\uD654\uD558\uAE30 \uC704\uD574 \uD604\uD589 \uD734\uC9C1\uC218\uB2F9 \uC0C1\uD55C\uC120\uC744 100\uB9CC \uC6D0\uC5D0\uC11C 200\uB9CC \uC6D0\uC73C\uB85C \uB192\uC774\uACE0, \uD1B5\uC0C1\uC784\uAE08\uC758 40%\uB97C \uC8FC\uAC8C \uB3FC \uC788\uB294 \uC721\uC544\uD734\uC9C1\n  \uC218\uB2F9 \uAE09\uC5EC\uC728\uB3C4 60%\uB85C \uC0C1\uD5A5 \uC870\uC815\uD588\uB2E4. \uB2E4\uB9CC \uAE30\uC5C5\uC5D0\uC11C \uC721\uC544\uD734\uC9C1\uC744 \uC4F0\uC9C0 \uBABB\uD558\uAC8C \uD560 \uACBD\uC6B0\uC758 \uCC98\uBC8C \uC870\uD56D\uC740 \uD604\uC2E4\uACFC \uAD34\uB9AC\uAC00 \uCEE4\uC9C4\uB2E4\uB294 \uC774\uC720\uB85C \uD3EC\uD568\uD558\uC9C0 \uC54A\uC558\uB2E4.',
