            description: '\'\uB9CC 8\uC138 \uB610\uB294 \uCD08\uB4F1\uD559\uAD50 2\uD559\uB144 \uC774\uD558\'\uC758 \uC790\uB140\uB85C\uB9CC \uD55C\uC815\uB410\uB358 \uAE30\uC874 \uC721\uC544\uD734\uC9C1\uC81C \uC801\uC6A9 \uB300\uC0C1\uC744 \'\uB9CC 18\uC138 \uB610\uB294 \uACE0\uB4F1\uD559\uAD50 3\uD559\uB144 \uC774\uD558\'\uB85C \uB113\uD614\uB2E4. \uC721\uC544\uD734\uC9C1\uC744\n  3\uCC28\uB840\uAE4C\uC9C0 \uB098\uB220 \uC4F8 \uC218 \uC788\uAE30 \uB54C\uBB38\uC5D0, \uC790\uB140\uC758 \uC131\uC7A5 \uB2E8\uACC4\uC5D0 \uB9DE\uCDB0 \uD0C4\uB825\uC801\uC73C\uB85C \uD734\uC9C1\uC744 \uC0AC\uC6A9\uD560 \uC218 \uC788\uB2E4.',
