            description: '\uC0C1\uC2DC\uADFC\uB85C\uC790 \uC218\uAC00 100\uBA85 \uC774\uC0C1\uC778 \uBBFC\uAC04\uAE30\uC5C5\uC758 \uC0AC\uC5C5\uC8FC\uB294 \uB9E4\uB144 \uC0C1\uC2DC\uADFC\uB85C\uC790 \uC218\uC758 2.5% \uBC94\uC704\uC5D0\uC11C \uC77C\uC815\xA0\uBE44\uC728(\uCCAD\uB144 \uBBF8\uCDE8\uC5C5\uC790 \uACE0\uC6A9\uB960)\xA0\uC774\uC0C1\uC529 \uCCAD\uB144 \uBBF8\uCDE8\uC5C5\uC790\uB97C \uACE0\uC6A9\uD558\uB3C4\uB85D \uB178\uB825\uD558\uB77C\uB294 \uB0B4\uC6A9\uC744 \uB2F4\uC740 \uBC95\uC548\uC744 \uBC1C\uC758\uD588\uB2E4.',
