            description: '2018\uB144 \uC9C0\uBC29\uC120\uAC70\uC5D0\uC11C \uC0AC\uAD50\uC721 \uD3D0\uC9C0\uC5D0 \uB300\uD55C \uAD6D\uBBFC\uD22C\uD45C\uB97C \uC2E4\uC2DC\uD574 \uAD6D\uBBFC\uC758 \uB73B\uC744 \uBB3B\uACE0, \uAD6D\uBBFC \uB2E4\uC218\uAC00 \uB3D9\uC758\uD55C\uB2E4\uBA74 \uC0AC\uAD50\uC721 \uC804\uBA74 \uD3D0\uC9C0\uB97C \uC704\uD55C \'\uAD50\uC721 \uAE40\uC601\uB780\uBC95\'\uC744 \uC81C\uC815\uD558\uACA0\uB2E4. \uB3D9\uC2DC\uC5D0 \uADFC\uBCF8\uC801\uC73C\uB85C \uC0AC\uAD50\uC721\uC774 \uD544\uC694 \uC5C6\uB294 \uC0AC\uD68C\uB97C \uB9CC\uB4E4\uAE30 \uC704\uD574 \uACF5\uAD50\uC721\uC744 \uC815\uC0C1\uD654\uD558\uACA0\uB2E4.',
