            summary: '\u201C\uB300\uD559 \uB4F1\uB85D\uAE08\uC758 45%\uC9C0\uC6D0\u201D',
            description: '\u201C\uB300\uD559 \uB4F1\uB85D\uAE08\uC758\xA045%\uB97C \uC9C0\uC6D0\uD558\uACE0 \uB9E4\uB144 \uC9C0\uC6D0 \uBE44\uC728\uC744 \uB298\uB824 2022\uB144\uC5D0\uB294 \uB4F1\uB85D\uAE08\uC758 75%\uB97C \uC9C0\uC6D0\uD1A0\uB85D \uD558\uACA0\uB2E4. \uC774\uB150\uC774\uB098 \uD3EC\uD4F0\uB9AC\uC998\uACFC \uAC19\uC740 \uC7A3\uB300\uB97C\xA0\uB4E4\uC774\uB300\uC11C\uB294 \uC548\uB41C\uB2E4. \uBBF8\uAD6D\uC744 \uC81C\uC678\uD558\uBA74 \uC6B0\uB9AC\uB098\uB77C\uCC98\uB7FC\xA0\uB300\uD559 \uB4F1\uB85D\uAE08\uC774\xA0\uBE44\uC2FC \uB098\uB77C\uB294 \uC5C6\uB2E4. \uB4F1\uB85D\uAE08 \uBD80\uB2F4\uC774 \uB108\uBB34 \uAC00\uC911\uD558\uB2E4\u201D',
