            description: '\uBCD1\uBB34\uBE44\uB9AC, \uBC29\uC0B0\uBE44\uB9AC, \uAD70\uB300 \uB0B4 \uAC00\uD639\uD589\uC704 \uB4F1\uC5D0 \uB300\uD55C \'3\uB300 \uBB34\uAD00\uC6A9\' \uC6D0\uCE59\uC5D0 \uB530\uB77C \'\uC6D0\uC2A4\uD2B8\uB77C\uC774\uD06C \uC544\uC6C3\'\uC81C\uB97C \uC2DC\uD589\uD574 \uAD70 \uAE30\uAC15\uC744 \uBC14\uB85C \uC138\uC6B0\uACA0\uB2E4\uB294 \uBC29\uC548\uC744\xA0\uC548\uBCF4 \uACF5\uC57D\uC5D0\xA0\uD3EC\uD568\uC2DC\uCF30\uB2E4.',
