            description: '\uB300\uD55C\uBBFC\uAD6D\uC758 \uC0C8\uB85C\uC6B4 \uBD80\uAD6D\uAC15\uBCD1\uC740 \'\uD55C\uAD6D\uD615 \uC790\uC8FC\uAD6D\uBC29\uB825\'\uC744 \uD0A4\uC6B0\uB294 \uB370\uC5D0\uC11C \uC2DC\uC791\uD55C\uB2E4\uBA70, \uC774\uB97C \uC704\uD574 \uD3C9\uD654\uC801 \uD575 \uC8FC\uAD8C\uC744 \uD589\uC0AC\uD558\uB294 \uCC28\uC6D0\uC5D0\uC11C \uB300\uC751\uC801, \uC790\uC704\uC801 \uD575\uBB34\uC7A5 \uAC00\uB2A5\uC131\uC744 \uAC80\uD1A0\uD558\uACA0\uB2E4\uACE0 \uBC1D\uD614\uB2E4. \uB610\uD55C \uC804\uC2DC\uC791\uC804\uAD8C\uC744 \uC870\uAE30\uC5D0 \uD658\uC218\uD574 \uC6B0\uB9AC \uC548\uBCF4 \uBB38\uC81C\uC5D0 \uB300\uD574 \uC6B0\uB9AC\uAC00 \uC8FC\uB3C4\uC801 \uC758\uC0AC\uACB0\uC815\uAD8C\uC744 \uD589\uC0AC\uD574\uC57C \uD55C\uB2E4\uB294 \uC810\uB3C4 \uAC15\uC870\uD588\uB2E4.',
